import React from 'react';
import { navigate } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import CreateIcon from '@mui/icons-material/Create';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import Link from '../components/link';

const ArticleList = props => {
  const { post } = props;
  const { currentTag } = props;

  const title = post.frontmatter.title || post.fields.slug;

  return (
    <>
      <ListItem
        sx={{
          // mt: i === 0 ? 0 : 3,
          mt: 3,
          mb: 3
        }}
        disablePadding={true}
      >
        <Box
          component='article'
          itemScope
          itemType='http://schema.org/Article'
          sx={{
            width: '100%'
          }}
        >
          <Box component='header'>
            <Link href={`/articles${post.fields.slug}`} underline='hover'>
              <Typography
                variant='h2'
                itemProp='headline'
                sx={{
                  display: 'flex',
                  alignItems: 'center'
                }}
              >
                {title}
              </Typography>
            </Link>
            <Box display='flex' alignItems='center' my={1}>
              <CreateIcon
                sx={{
                  fontSize: 16
                }}
              />
              <Typography variant='body2'>{post.frontmatter.date}</Typography>
            </Box>
          </Box>
          <Stack
            direction='row'
            spacing={1}
            my={1}
            sx={{
              overflow: 'scroll'
            }}
          >
            {post.frontmatter?.tags?.map(tag => {
              const isCurrentTag = tag === currentTag;

              return (
                <Chip
                  key={tag}
                  label={tag}
                  color={isCurrentTag ? 'primary' : undefined}
                  onClick={
                    isCurrentTag ? undefined : () => navigate(`/tags/${tag}/`)
                  }
                  icon={
                    <LocalOfferIcon
                      sx={{
                        fontSize: 16
                      }}
                    />
                  }
                />
              );
            })}
          </Stack>
          <Box component='section'>
            <Link
              href={`/articles${post.fields.slug}`}
              sx={{
                float: 'left',
                mr: 1,
                mb: 1
              }}
            >
              {post.frontmatter.thumbnail ? (
                <GatsbyImage
                  image={getImage(post.frontmatter.thumbnail)}
                  alt={title}
                  style={{
                    width: '100px'
                  }}
                />
              ) : (
                <img
                  src={`https://images-na.ssl-images-amazon.com/images/P/${post.frontmatter.asins[0]}.09.LZZZZZZZ`}
                  alt={title}
                  width={120}
                  height={160}
                  loading='lazy'
                  style={{
                    display: 'block',
                    objectFit: 'contain',
                    border: '1px solid #ccc',
                    boxSizing: 'border-box'
                  }}
                />
              )}
            </Link>
            <Typography
              dangerouslySetInnerHTML={{
                __html: post.frontmatter.description || post.excerpt
              }}
              itemProp='description'
            />
          </Box>
        </Box>
      </ListItem>
      <Divider
        sx={{
          my: 5
        }}
      />
    </>
  );
};

export default ArticleList;
